$main-font: Vazirmatn RD UI FD, sans-serif;
$accent-color: #20c5ba;

/* RTL */

html,
body {
    direction: rtl;
}

/* custom styles */

img {
    opacity: 1 !important;
}

a {
    transition: all 150ms linear;
}

.post-card {
    .post-card-image {
        border-radius: 10px;
    }

    .post-card-title {
        direction: rtl;
        font-size: 2.5rem;
        font-family: $main-font;
        color: white;
    }

    .post-card-excerpt {
        direction: rtl;
        text-align: justify;
        font-family: $main-font;

        p {
            font-size: 1.5rem;
        }
    }

    .post-card-meta {
        direction: ltr;
        align-self: flex-start;
    }
}

.post {
    .post-card-image {
        opacity: 0.9 !important;
        transition: opacity 200ms linear;

        &:hover {
            opacity: 1 !important;
        }
    }

    .author-name {
        font-family: $main-font;
    }

    .article-header {
        .article-title {
            direction: rtl;
            font-family: $main-font;
            font-size: clamp(3rem, 5vw, 5rem);
        }

        .article-excerpt {
            direction: rtl;
            font-family: $main-font;
        }

        .article-byline {
            .article-byline-meta {
                .author-name {
                    margin: 0 6px 0 0;
                }
                .byline-meta-content {
                    margin: 6px 6px 0 0;
                }
            }
        }

        .article-image img {
            border-radius: 50px;
        }
    }

    .article-tag {
        margin-bottom: 20px;

        a {
            padding: 3px 6px 0px 6px;
            font-family: $main-font;
        }

        .post-card-featured {
            padding-left: 0;
            padding-right: 18px;

            svg {
                left: unset !important;
                right: 0;
            }
        }
    }
}

.gh-content {
    p {
        color: white;
        direction: rtl;
        text-align: justify;
        text-align-last: right;
        font-family: $main-font;
        font-size: 2rem;
        font-weight: 300;
        letter-spacing: 0.41px;

        a {
            text-decoration: none !important;
            transition: color 250ms;

            &:hover {
                color: $accent-color;
            }
        }

        strong {
            font-weight: 500;
            color: #91deda;
        }

        code {
            word-break: break-word;
            padding: 3px 5px 3px 5px;
            unicode-bidi: plaintext;
        }
    }

    ul,
    ol {
        font-family: $main-font;

        li {
            font-size: 16px;

            &::marker {
                color: $accent-color;
            }
        }
    }

    pre {
        direction: ltr !important;
        border-radius: 10px;
        background: #292d3e;
        border: none;
    }

    h2,
    h3,
    h4 {
        font-family: $main-font;
        color: $accent-color !important;
    }

    figure {
        img {
            border-radius: 10px;
        }

        .kg-bookmark-container {
            direction: ltr;
            border-radius: 10px;

            .kg-bookmark-title,
            .kg-bookmark-description {
                direction: rtl;
                font-family: $main-font;
            }
        }
    }

    blockquote {
        font-family: $main-font;
        font-size: 16px;
        color: #92a3ab;
        border-right: 3px solid $accent-color;
        border-left: 3px solid $accent-color;
        padding: 0 10px;
    }

    img {
        max-width: 100%;
        border-radius: 10px;
    }

    video {
        max-width: 100%;
    }
}

.nav {
    li {
        a {
            font-family: $main-font;
        }
    }
}

.site-footer {
    padding-bottom: 40px;
    padding-top: 40px;
}

.gh-head-brand .gh-search {
    margin-right: 0;
    margin-left: 8px;
}

.has-cover .site-header-content {
    min-height: 506px;
}

.site-header-cover {
    opacity: 0.75 !important;
}

@media (max-width: 767px) {
    #gh-head .gh-head-brand .gh-search {
        margin-left: 0;
        margin-right: -6px;
    }
}
